import { Component } from '@angular/core';
import { IonicPage, AlertController, NavController, LoadingController, NavParams, ToastController } from 'ionic-angular';
import { UserProvider } from '../../providers/user/user';
import { TranslatePipe, TranslateService, LangChangeEvent } from '@ngx-translate/core';

@IonicPage({
  name: 'page-add-amount',
  segment: 'add-amount'
})

@Component({
  selector: 'page-add-amount',
  templateUrl: 'add-amount.html'
})

export class AddAmountPage {
  bankname: any;
  amount1: any;
  itemone: any;
  share: any;
  total: any;
  legalid: any;
  specialid: any;
  Name: any;
  DECIMAL_SEPARATOR=".";
  GROUP_SEPARATOR=",";
  constructor(public navCtrl: NavController, public translate: TranslateService, public navparams: NavParams, public user: UserProvider, public alertCtrl: AlertController, public loadingCtrl: LoadingController, public toastCtrl: ToastController) {
    this.translate.setDefaultLang('en');
    this.translate.use('es');
    this.bankname = this.navparams.get('bankname');
    this.itemone = this.navparams.get('data');

    console.log(this.itemone, 'dddddd');
    if (this.itemone != undefined) {
      this.amount1 = this.itemone.amount.toFixed(2);
    var num_parts = this.amount1.toString().split(".");
    num_parts[0] = num_parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    this.amount1 = num_parts.join(".");
      //  this.amount1 = parseInt(this.format(this.amount1));
      this.share = this.itemone.shares;
      this.total = this.itemone.Totalamount;
      this.legalid = this.itemone.legalid;
      this.specialid = this.itemone.specialid;
      this.Name = this.itemone.Name;
      console.log(this.amount1, this.share, this.total, this.legalid, this.specialid, this.Name);
      if (this.specialid === 1) {
        var storedNames = JSON.parse(localStorage.getItem("chekout"));
        console.log(storedNames);
        if (storedNames != null) {

          storedNames.push({
            shares: this.share,
            ShareValue: this.total,
            legalId: this.legalid,
            Name: this.Name

          })

          localStorage.setItem("chekout", JSON.stringify(storedNames));
        } else {
          var names = [];
          names.push({
            shares: this.share,
            ShareValue: this.total,
            legalId: this.legalid,
          })

          localStorage.setItem("chekout", JSON.stringify(names));
        }
      }
    } else {

      this.itemone = this.navparams.get('data1');


    }

    


  }

  format(valString) {
    // alert(valString);
    if (!valString) {
        return '';
    }
    let val = valString.toString();
    const parts = this.unFormat(val).split(this.DECIMAL_SEPARATOR);
    return parts[0].replace(/\B(?=(?:\d{3})+(?!\d))/g, this.GROUP_SEPARATOR) + (!parts[1] ? '' : this.DECIMAL_SEPARATOR + parts[1]);
};

unFormat(val) {
    if (!val) {
        return '';
    }
    val = val.replace(/^0+/, '');

    if (this.GROUP_SEPARATOR === ',') {
        return val.replace(/,/g, '');
    } else {
        return val.replace(/\./g, '');
    }
};


  // check userDepositETF go to make-deposit page
  next() {

  

    this.user.userDeposit(this.unFormat(this.amount1)).subscribe(
      data => {
        let datas = JSON.parse(JSON.stringify(data));
        let data2 = JSON.parse(datas._body);
        if (data2.success) {



          this.navCtrl.setRoot('page-deposit-money', { 'data': data2.data, 'amount': this.unFormat(this.amount1)});

        } else {

          this.translate.get('deposit-page.ol.next_btn_error_toast').subscribe((translated: string) => {
            var next_btn_error_toast = this.translate.instant('deposit-page.ol.next_btn_error_toast');

            let toast = this.toastCtrl.create({
              message: next_btn_error_toast,
              duration: 3000,
              position: 'top'
            });

            toast.onDidDismiss(() => {
              console.log('Dismissed toast');

            });

            toast.present();
          })
        }
      }, error => {
        console.log(error.JSON);
        setTimeout(() => {
          this.navCtrl.setRoot('page-general_error');
        }, 1000);
      },
    )



  }

}



